.product-detail{padding:2rem 0 4rem}.product-detail .loading-state{min-height:60vh;display:flex;align-items:center;justify-content:center}.product-detail .product-detail-content{display:grid;grid-template-columns:1fr 1fr;gap:4rem}@media(max-width: 968px){.product-detail .product-detail-content{grid-template-columns:1fr;gap:3rem}}.product-detail .product-images .main-image{position:relative;margin-bottom:1rem;border-radius:var(--border-radius-lg);overflow:hidden;aspect-ratio:4/3}.product-detail .product-images .main-image img{width:100%;height:100%;object-fit:cover}.product-detail .product-images .main-image .out-of-stock-overlay{position:absolute;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,.7);color:var(--white);display:flex;align-items:center;justify-content:center;font-size:1.5rem;font-weight:600}.product-detail .product-images .main-image .discount-badge{position:absolute;top:1rem;right:1rem;background:var(--success-color);color:var(--white);padding:.5rem 1rem;border-radius:var(--border-radius);font-weight:600}.product-detail .product-images .image-thumbnails{display:flex;gap:.75rem;overflow-x:auto}.product-detail .product-images .image-thumbnails .thumbnail{flex-shrink:0;width:80px;height:60px;border:2px solid rgba(0,0,0,0);border-radius:var(--border-radius);overflow:hidden;background:none;cursor:pointer;transition:var(--transition)}.product-detail .product-images .image-thumbnails .thumbnail.active{border-color:var(--primary-color)}.product-detail .product-images .image-thumbnails .thumbnail img{width:100%;height:100%;object-fit:cover}.product-detail .product-images .image-thumbnails .thumbnail:hover{border-color:var(--primary-light)}.product-detail .product-info .product-header{margin-bottom:2rem}.product-detail .product-info .product-category{color:var(--text-muted);font-size:.9rem;text-transform:uppercase;letter-spacing:.05em;margin-bottom:.5rem}.product-detail .product-info .product-title{color:var(--text-dark);margin-bottom:1rem;line-height:1.2}.product-detail .product-info .product-rating{display:flex;align-items:center;gap:.75rem;margin-bottom:1.5rem}.product-detail .product-info .product-rating .stars{display:flex;gap:.125rem}.product-detail .product-info .product-rating .stars .star{font-size:1rem;opacity:.3}.product-detail .product-info .product-rating .stars .star.filled{opacity:1}.product-detail .product-info .product-rating .rating-text{color:var(--text-light);font-size:.9rem}.product-detail .product-info .product-pricing{display:flex;align-items:center;gap:1rem;margin-bottom:2rem}.product-detail .product-info .product-pricing .current-price{font-size:2rem;font-weight:700;color:var(--primary-color)}.product-detail .product-info .product-pricing .original-price{font-size:1.25rem;color:var(--text-muted);text-decoration:line-through}.product-detail .product-info .product-pricing .discount-text{background:var(--success-color);color:var(--white);padding:.25rem .75rem;border-radius:var(--border-radius);font-size:.875rem;font-weight:600}.product-detail .product-info .product-description{margin-bottom:2rem}.product-detail .product-info .product-description p{font-size:1.1rem;line-height:1.7}.product-detail .product-info .product-features{margin-bottom:2rem}.product-detail .product-info .product-features h3{color:var(--primary-color);margin-bottom:1rem}.product-detail .product-info .product-features ul{list-style:none;padding:0}.product-detail .product-info .product-features ul li{padding:.5rem 0;border-bottom:1px solid var(--border-color);display:flex;align-items:center}.product-detail .product-info .product-features ul li:before{content:"✓";color:var(--success-color);font-weight:bold;margin-right:.75rem}.product-detail .product-info .product-features ul li:last-child{border-bottom:none}.product-detail .product-info .product-options{margin-bottom:2rem}.product-detail .product-info .product-options h4{color:var(--text-dark);margin-bottom:.75rem;font-size:1rem}.product-detail .product-info .product-options .color-options{margin-bottom:1.5rem}.product-detail .product-info .product-options .color-options .color-selector{display:flex;gap:.75rem}.product-detail .product-info .product-options .color-options .color-selector .color-option{padding:.5rem 1rem;border:2px solid var(--border-color);border-radius:var(--border-radius);background:var(--white);cursor:pointer;transition:var(--transition)}.product-detail .product-info .product-options .color-options .color-selector .color-option.active,.product-detail .product-info .product-options .color-options .color-selector .color-option:hover{border-color:var(--primary-color);background:var(--light-gray)}.product-detail .product-info .product-options .quantity-selector .quantity-controls{display:flex;align-items:center;gap:1rem}.product-detail .product-info .product-options .quantity-selector .quantity-controls .quantity-btn{width:40px;height:40px;border:1px solid var(--border-color);border-radius:var(--border-radius);background:var(--white);cursor:pointer;font-size:1.25rem;display:flex;align-items:center;justify-content:center;transition:var(--transition)}.product-detail .product-info .product-options .quantity-selector .quantity-controls .quantity-btn:hover{border-color:var(--primary-color);background:var(--light-gray)}.product-detail .product-info .product-options .quantity-selector .quantity-controls .quantity-display{font-size:1.25rem;font-weight:600;min-width:40px;text-align:center}.product-detail .product-info .product-actions{display:flex;gap:1rem;margin-bottom:2rem}@media(max-width: 480px){.product-detail .product-info .product-actions{flex-direction:column}}.product-detail .product-info .product-actions .add-to-cart{flex:1}.product-detail .product-info .product-actions .wishlist{flex:0 0 auto}.product-detail .product-info .product-specifications h3{color:var(--primary-color);margin-bottom:1rem}.product-detail .product-info .product-specifications .spec-grid{display:grid;gap:.75rem}.product-detail .product-info .product-specifications .spec-grid .spec-item{display:flex;justify-content:space-between;align-items:center;padding:.75rem 0;border-bottom:1px solid var(--border-color)}.product-detail .product-info .product-specifications .spec-grid .spec-item:last-child{border-bottom:none}.product-detail .product-info .product-specifications .spec-grid .spec-item .spec-label{font-weight:500;color:var(--text-dark)}.product-detail .product-info .product-specifications .spec-grid .spec-item .spec-value{color:var(--text-light);text-align:right}