.hero {
  background: linear-gradient(135deg, var(--light-gray) 0%, var(--white) 100%);
  padding: 100px 0 80px;
  overflow: hidden;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><circle cx="25" cy="25" r="2" fill="%23e9ecef" opacity="0.5"/><circle cx="75" cy="75" r="1.5" fill="%238bc34a" opacity="0.3"/><circle cx="90" cy="10" r="1" fill="%23ffc107" opacity="0.4"/></svg>') repeat;
    opacity: 0.1;
    z-index: 1;
  }

  .hero-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    align-items: center;
    position: relative;
    z-index: 2;

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      gap: 3rem;
      text-align: center;
    }
  }

  .hero-text {
    animation: fadeInUp 0.8s ease-out;
  }

  .hero-title {
    margin-bottom: 1.5rem;
    line-height: 1.2;
    
    .text-secondary {
      display: block;
      margin-top: 0.5rem;
    }
  }

  .hero-description {
    font-size: 18px;
    margin-bottom: 2.5rem;
    max-width: 500px;
    
    @media (max-width: 768px) {
      margin: 0 auto 2.5rem;
    }
  }

  .hero-actions {
    display: flex;
    gap: 1rem;
    margin-bottom: 3rem;
    
    @media (max-width: 480px) {
      flex-direction: column;
      align-items: center;
    }
  }

  .hero-stats {
    display: flex;
    gap: 2rem;
    
    @media (max-width: 768px) {
      justify-content: center;
    }
    
    @media (max-width: 480px) {
      gap: 1rem;
    }

    .stat {
      text-align: center;

      .stat-number {
        font-size: 2rem;
        font-weight: 700;
        color: var(--primary-color);
        line-height: 1;
        margin-bottom: 0.25rem;
      }

      .stat-label {
        font-size: 0.9rem;
        color: var(--text-muted);
        font-weight: 500;
      }
    }
  }

  .hero-image {
    position: relative;
    animation: fadeInUp 0.8s ease-out 0.2s both;
    
    @media (max-width: 768px) {
      order: -1;
    }
  }

  .hero-illustration {
    .furniture-illustration {
      width: 100%;
      height: auto;
      max-width: 400px;
      animation: float 6s ease-in-out infinite;
    }
  }

  
    .floating-badges {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;

    .badge {
      position: absolute;
      background: var(--white);
      padding: 0.5rem 1rem;
      border-radius: var(--border-radius-xl);
      box-shadow: var(--shadow-medium);
      display: flex;
      align-items: center;
      gap: 0.5rem;
      font-size: 0.875rem;
      font-weight: 500;
      animation: float 4s ease-in-out infinite;

      .badge-icon {
        font-size: 1.2rem;
      }

      &.badge-1 {
        top: 20%;
        right: 10%;
        animation-delay: 0s;
      }

      &.badge-2 {
        top: 60%;
        right: 0%;
        animation-delay: 1s;
      }

      &.badge-3 {
        bottom: 20%;
        right: 20%;
        animation-delay: 2s;
      }
    }
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}