.products-page{padding:2rem 0 4rem;min-height:80vh}.products-page .page-header{text-align:center;margin-bottom:3rem}.products-page .page-header h1{margin-bottom:1rem}.products-page .page-header p{font-size:1.1rem;color:var(--text-light);max-width:600px;margin:0 auto}.products-page .products-layout{display:grid;grid-template-columns:280px 1fr;gap:3rem}@media(max-width: 968px){.products-page .products-layout{grid-template-columns:1fr;gap:2rem}}.products-page .filters-sidebar{background:var(--white);padding:2rem;border-radius:var(--border-radius-lg);box-shadow:var(--shadow-light);height:fit-content;position:sticky;top:90px}@media(max-width: 968px){.products-page .filters-sidebar{position:static}}.products-page .filters-sidebar .filters-header{display:flex;justify-content:space-between;align-items:center;margin-bottom:2rem;padding-bottom:1rem;border-bottom:1px solid var(--border-color)}.products-page .filters-sidebar .filters-header h3{margin:0;color:var(--primary-color)}.products-page .filters-sidebar .filter-group{margin-bottom:1.5rem}.products-page .filters-sidebar .filter-group label{display:block;font-weight:500;color:var(--text-dark);margin-bottom:.5rem}.products-page .filters-sidebar .filter-group select{width:100%;padding:.75rem;border:1px solid var(--border-color);border-radius:var(--border-radius);background:var(--white);font-family:var(--font-primary);transition:var(--transition)}.products-page .filters-sidebar .filter-group select:focus{outline:none;border-color:var(--primary-color);box-shadow:0 0 0 3px rgba(45,90,39,.1)}.products-page .filters-sidebar .filter-group .checkbox-label{display:flex;align-items:center;cursor:pointer;position:relative}.products-page .filters-sidebar .filter-group .checkbox-label input[type=checkbox]{opacity:0;position:absolute}.products-page .filters-sidebar .filter-group .checkbox-label .checkmark{width:20px;height:20px;border:2px solid var(--border-color);border-radius:var(--border-radius);margin-right:.75rem;display:flex;align-items:center;justify-content:center;transition:var(--transition)}.products-page .filters-sidebar .filter-group .checkbox-label .checkmark::after{content:"✓";color:var(--white);font-size:.875rem;opacity:0;transition:var(--transition)}.products-page .filters-sidebar .filter-group .checkbox-label input[type=checkbox]:checked+.checkmark{background-color:var(--primary-color);border-color:var(--primary-color)}.products-page .filters-sidebar .filter-group .checkbox-label input[type=checkbox]:checked+.checkmark::after{opacity:1}.products-page .products-content .products-header{margin-bottom:2rem}.products-page .products-content .products-header .results-info{color:var(--text-light);font-weight:500}.products-page .products-content .products-grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(280px, 1fr));gap:2rem}@media(max-width: 768px){.products-page .products-content .products-grid{grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));gap:1.5rem}}.products-page .products-content .products-grid>div{animation:fadeInUp .4s ease-out both}.products-page .products-content .no-products{text-align:center;padding:4rem 2rem}.products-page .products-content .no-products .no-products-icon{font-size:4rem;margin-bottom:1rem;opacity:.5}.products-page .products-content .no-products h3{color:var(--text-dark);margin-bottom:1rem}.products-page .products-content .no-products p{color:var(--text-light);margin-bottom:2rem}